import { lego } from '@armathai/lego';
import { getFreeSpinButtonConfig, getSpinButtonConfig, getStopButtonConfig } from '../../configs/button-configs';
import { SlotMachineState, SpinBtnState } from '../../constants';
import { ModelEvents } from '../../events/model-events';
import { ViewEvents } from '../../events/view-events';
import { store } from '../../models/store';
import { Button, FreeSpinButton } from '../../utils/button/button';
import { Container } from '../../utils/container';

export class SpinView extends Container {
  constructor() {
    super();
    this.name = 'SpinView';

    this._spinBtn = null;
    this._stopBtn = null;
    this._freeSpinBtn = null;
    this._activeBtn = null;

    this._build();

    lego.event
      .on(ModelEvents.SpinButtonModel.StateUpdate, this._onSpinStateUpdate, this)
      .on(ModelEvents.SpinButtonModel.ActiveUpdate, this._onSpinActiveUpdate, this)
      .on(ModelEvents.SlotMachineModel.StateUpdate, this._onSlotMachineStateUpdate, this);

    // setTimeout(() => {
    //   this._setFreeSpinsState();
    //   this._activeBtn.switchEnable(true);
    // }, 1000);
  }

  _onSpinStateUpdate(value) {
    switch (value) {
      case SpinBtnState.FreeSpin:
        this._setFreeSpinsState();
        break;
      case SpinBtnState.Spin:
        this._setSpinState();
        break;
      case SpinBtnState.Stop:
        this._setStopState();
        break;
      default:
    }
  }

  _onSpinActiveUpdate() {
    this._checkForButtonActive();
  }

  _checkForButtonsState() {
    const { state } = store.game.spinBtn;
    state === SpinBtnState.Spin ? this._setSpinState() : this._setStopState();
  }

  _onSlotMachineStateUpdate(state) {
    switch (state) {
      case SlotMachineState.Spin:
        this._updateFreeSpinBtnText();
        break;
      default:
    }
  }

  _setSpinState() {
    this._spinBtn.visible = true;
    this._stopBtn.visible = false;
    this._freeSpinBtn.visible = false;
    this._activeBtn = this._spinBtn;
  }

  _setStopState() {
    this._stopBtn.visible = true;
    this._spinBtn.visible = false;
    this._freeSpinBtn.visible = false;

    this._activeBtn = this._stopBtn;
  }

  _setFreeSpinsState() {
    this._stopBtn.visible = false;
    this._spinBtn.visible = false;
    this._freeSpinBtn.visible = true;
    this._activeBtn = this._freeSpinBtn;

    this.bringToTop(this._freeSpinBtn);
  }

  _checkForButtonActive() {
    const { active } = store.game.spinBtn;
    this._activeBtn.switchEnable(active);
  }

  _updateFreeSpinBtnText() {
    const { slotMachine } = store.game;
    const { spinsCount } = slotMachine;
    const { number_of_extra_spins, first_slot_spins } = CI_API.Globals.PARAMS;

    this._freeSpinBtn.setText({
      text: `${number_of_extra_spins - (spinsCount - first_slot_spins + 1)} / ${number_of_extra_spins}`,
      ignoreLocalization: true,
      toString: function toString() {
        return this;
      }
    });
  }

  _build() {
    const freeSpinBtn = new FreeSpinButton(getFreeSpinButtonConfig());
    const spinBtn = new Button(getSpinButtonConfig());
    const stopBtn = new Button(getStopButtonConfig());

    freeSpinBtn.onClick.add(this._onSpinClick, this);
    spinBtn.onClick.add(this._onSpinClick, this);
    stopBtn.onClick.add(this._onStopClick, this);

    freeSpinBtn.switchEnable(false);
    spinBtn.switchEnable(false);
    stopBtn.switchEnable(false);

    this.addChild((this._freeSpinBtn = freeSpinBtn));
    this.addChild((this._spinBtn = spinBtn));
    this.addChild((this._stopBtn = stopBtn));
  }

  // eslint-disable-next-line class-methods-use-this
  _onSpinClick() {
    lego.event.emit(ViewEvents.SpinView.Spin);
  }

  // eslint-disable-next-line class-methods-use-this
  _onStopClick() {
    lego.event.emit(ViewEvents.SpinView.Stop);
  }
}
