import { lego } from '@armathai/lego';
import { SlotMachineState } from '../../../constants';
import { slotMachineStateActionCommand } from './slot-machine-state-action-command';
import { slotMachineStateIdleCommand } from './slot-machine-state-idle-command';
import { slotMachineStateMaxSpeedCommand } from './slot-machine-state-max-speed-command';
import { slotMachineStateSpinCommand } from './slot-machine-state-spin-command';
import { slotMachineStateStopCommand } from './slot-machine-state-stop-command';

export function slotMachineStateUpdateCommand(state) {
  switch (state) {
    case SlotMachineState.Spin:
      lego.command.execute(slotMachineStateSpinCommand);
      break;
    case SlotMachineState.MaxSpeed:
      lego.command.execute(slotMachineStateMaxSpeedCommand);
      break;
    case SlotMachineState.Stop:
      lego.command.execute(slotMachineStateStopCommand);
      break;
    case SlotMachineState.Action:
      lego.command.execute(slotMachineStateActionCommand);
      break;
    case SlotMachineState.Idle:
      lego.command.execute(slotMachineStateIdleCommand);
      break;
    default:
  }
}
