/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { Phaser2Grid } from '@armathai/phaser2-grid';
import { getRewardGridConfig } from '../../../configs/grid-configs';
import { GameStatus, InputPriority } from '../../../constants';
import { ModelEvents } from '../../../events/model-events';
import { ViewEvents } from '../../../events/view-events';
import { delayRunnable, makePixel, removeRunnable } from '../../../utils';
import { SparkleEmitter } from '../../../utils/emitters/sparkle/sparkle-emitter';
import { RewardPopup } from './reward-popup';

export class RewardView extends Phaser2Grid {
  constructor() {
    super(CI_API.game);
    super.build(this.getGridConfig());

    this._blocker = null;
    this._hideRunnable = null;

    lego.event
      .on(ModelEvents.GameModel.StatusUpdate, this._onGameStatusUpdate, this)
      .on(ModelEvents.CtaModel.VisibleUpdate, this._onCtaVisibleUpdate, this);
  }

  _onCtaVisibleUpdate(value) {
    if (!value) {
      return;
    }

    this.visible = false;
    removeRunnable(this._hideRunnable);
  }

  getGridConfig() {
    return getRewardGridConfig();
  }

  _onGameStatusUpdate(status) {
    switch (status) {
      case GameStatus.Reward:
        this._init();
        break;
      default:
    }
  }

  _init() {
    this._buildBlocker();
    this._buildEmitter();
    this._buildPopup();
    this._show();
  }

  _buildBlocker() {
    const blocker = makePixel({ tint: 0x0, alpha: 0.7 });
    blocker.inputEnabled = true;
    blocker.input.priorityID = InputPriority.Reward;

    this.setChild('blocker', (this._blocker = blocker));
  }

  _buildEmitter() {
    this.setChild('emitter', (this._emitter = new SparkleEmitter()));
  }

  _buildPopup() {
    this.setChild('popup', (this._popup = new RewardPopup()));
  }

  _show() {
    const { Cubic, Back } = Phaser.Easing;

    this.game.add.tween(this).from({ alpha: 0 }, 400, Cubic.Out, true);
    this.game.add
      .tween(this._popup.scale)
      .from({ x: 0, y: 0 }, 400, Back.Out, true)
      .onStart.addOnce(this._onShowStart, this);

    this._hideRunnable = delayRunnable(3000, this._hide, this);

    this._emitter.emit();
  }

  _hide() {
    const { Cubic } = Phaser.Easing;

    this.game.add
      .tween(this)
      .to({ alpha: 0 }, 400, Cubic.In, true)
      .onComplete.addOnce(this._onHideComplete, this);
  }

  _onShowStart() {
    lego.event.emit(ViewEvents.RewardView.ShowStart);
  }

  _onHideComplete() {
    this.visible = false;
    this._emitter.stop();

    lego.event.emit(ViewEvents.RewardView.HideComplete);
  }
}
