/* eslint-disable no-param-reassign */
/* eslint-disable no-plusplus */
function BackOut(k) {
  // const s = 1.70158;
  const s = 0.8;
  return --k * k * ((s + 1) * k + s) + 1;
}

function BackIn(k) {
  // const s = 1.70158;
  const s = 0.4;
  return k * k * ((s + 1) * k - s);
}

