import { lego } from '@armathai/lego';
import { ReelState, SlotMachineState } from '../../../constants';
import { lastReelGuard } from '../../../guards/game/last-reel-guard';
import { setReelStateCommand } from '../../game/machine/reel/set-reel-state-command';
import { setSlotMachineStateCommand } from '../../game/machine/set-slot-machine-state-command';

export function reelSpeedDownCompleteCommand(uuid) {
  lego.command
    .payload(uuid, ReelState.Idle)
    .execute(setReelStateCommand)

    .payload(SlotMachineState.Action)
    .guard(() => lastReelGuard(uuid))
    .execute(setSlotMachineStateCommand);
}
