import { lego } from '@armathai/lego';
import { ModelEvents } from '../../../events/model-events';
import { ViewEvents } from '../../../events/view-events';
import { store } from '../../../models/store';
import { loopRunnable, removeRunnable } from '../../../utils';
import { Container } from '../../../utils/container';
import { SlotView } from './slot-view';

function getTweenPoints(time, ease, from, to) {
  const dt = CI_API.game.time.physicsElapsedMS;
  const points = [];
  const framesCount = Math.round(((1 / time) * 1000) / dt);
  for (let i = 1; i <= framesCount; i += 1) {
    points.push(Math.round(from + ease(i / framesCount) * to));
  }

  return points;
}

export class ReelView extends Container {
  constructor(model) {
    super();

    this.name = 'ReelView';

    const { slots, uuid } = model;
    const { slotOffset, speed } = store.game.slotMachine.config;

    this._uuid = uuid;
    this._offset = slotOffset;
    this._speed = speed;
    this._loopRunnable = null;
    this._spinRunnable = null;
    this._stopRunnable = null;
    this._loopStep = null;
    this._slots = null;
    this._height = null;
    this._tile = null;

    lego.event.on(ModelEvents.SlotModel.TypeUpdate, this._onSlotTypeUpdate, this);

    this._build(slots);
  }

  get uuid() {
    return this._uuid;
  }

  get slots() {
    return this._slots;
  }

  get tileY() {
    return this._tileY;
  }

  set tileY(value) {
    this._tileY = value;
    this._updateSlotsPositions();
  }

  destroy() {
    removeRunnable(this._spinRunnable);
    removeRunnable(this._stopRunnable);
    removeRunnable(this._loopRunnable);

    super.destroy();
  }

  highlightSpinResult(slots) {
    slots.forEach(s => {
      const { type, index } = s;
      if (type < 10) {
        this._slots[index].highlightSpinResult();
      }
    });
  }

  getSlot(uuid) {
    return this._slots.find(slot => slot.uuid === uuid);
  }

  getSlotByIndex(index) {
    return this.slots[index];
  }

  spin() {
    const { spinEasing } = store.game.slotMachine.config;
    const points = getTweenPoints(this._speed, spinEasing, this._tileY, this._height);

    const lastPoints = points.slice(points.length - 2);
    this._loopStep = lastPoints[1] - lastPoints[0];
    this._spinRunnable = loopRunnable(0, () => {
      this.tileY = points.shift();

      if (!points.length) {
        removeRunnable(this._spinRunnable);
        this._loop();
        lego.event.emit(ViewEvents.ReelView.SpeedUpComplete, this._uuid);
      }
    });
  }

  stop() {
    removeRunnable(this._loopRunnable);

    this.unBlur();

    const { stopEasing } = store.game.slotMachine.config;
    const diff = this._height - (this._tileY % this._height);
    const points = getTweenPoints(this._speed, stopEasing, this._tileY, diff + this._height);

    this._stopRunnable = loopRunnable(0, () => {
      this.tileY = points.shift();
      if (!points.length) {
        removeRunnable(this._stopRunnable);
        lego.event.emit(ViewEvents.ReelView.SpeedDownComplete, this._uuid);
      }
    });
  }

  blur() {
    this._slots.forEach(s => s.blur());
  }

  unBlur() {
    this._slots.forEach(s => s.unBlur());
  }

  _onSlotTypeUpdate(newType, oldType, uuid) {
    const slotView = this.getSlot(uuid);
    if (!slotView) {
      return;
    }

    slotView.setType(newType);
    this._height = this._calculateHeight();
  }

  _loop() {
    this._loopRunnable = loopRunnable(0, () => {
      this.tileY += this._loopStep;
    });
  }

  _buildSlots(slots) {
    return slots.map(model => {
      const slot = new SlotView(model);

      return this.addChild(slot);
    });
  }

  _calculateHeight() {
    return this._slots.reduce((acc, cur) => acc + cur.height + this._offset, 0);
  }

  _updateSlotsPositions() {
    for (let i = 0; i < this._slots.length; i += 1) {
      const slot = this._slots[i];

      if (i === 0) {
        slot.setY(this._tileY % this._height);
      } else {
        const previews = this._slots[i - 1];
        slot.setY(previews.bottom + this._offset);
      }

      this._checkForLimits(slot);
    }
  }

  _checkForLimits(slot) {
    if (slot.bottom > this._height) {
      slot.setY(slot.top - this._height);
    } else if (slot.bottom < 0) {
      slot.setY(slot.top + this._height);
    }
  }

  _build(slots) {
    this._slots = this._buildSlots(slots);

    this._height = this._calculateHeight();
    this.tileY = 0;
  }
}
