import { SlotState } from '../../../constants';
import { ObservableModel } from '../../observable-model';

export class SlotModel extends ObservableModel {
  constructor(type) {
    super('SlotModel');

    this._type = type;
    this._state = SlotState.Idle;

    this.makeObservable();
  }

  get type() {
    return this._type;
  }

  set type(value) {
    this._type = value;
  }

  get state() {
    return this._state;
  }

  setType(value) {
    this._type = value;
  }

  setState(state) {
    this._state = state;
  }
}
