import { store } from '../../models/store';

export function autoSpinGuard() {
  const { first_slot_auto, extra_spin_auto, first_slot_spins } = CI_API.Globals.PARAMS;
  const { spinsCount } = store.game.slotMachine;

  if (first_slot_auto && spinsCount < first_slot_spins) {
    return true;
  }

  if (extra_spin_auto && spinsCount >= first_slot_spins) {
    return true;
  }

  return false;
}
