import { lego, not } from '@armathai/lego';
import { SpinBtnState } from '../../../constants';
import { extraSpinsGuard } from '../../../guards/game/extra-spins-guard';
import { stopButtonGuard } from '../../../guards/game/stop-button-guard';
import { setSpinButtonActiveCommand } from '../spin/set-spin-button-active-command';
import { setSpinButtonStateCommand } from '../spin/set-spin-button-state-command';

export function slotMachineStateMaxSpeedCommand() {
  lego.command
    .guard(not(extraSpinsGuard), stopButtonGuard)
    .payload(SpinBtnState.Stop)
    .execute(setSpinButtonStateCommand)

    .guard(stopButtonGuard)
    .payload(true)
    .execute(setSpinButtonActiveCommand);
}
