/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { getSlotMachineBgImageConfig } from '../../../configs/image-configs';
import { ReelState, SlotMachineState } from '../../../constants';
import { ModelEvents } from '../../../events/model-events';
import { ViewEvents } from '../../../events/view-events';
import { store } from '../../../models/store';
import { makeImage } from '../../../utils';
import { Container } from '../../../utils/container';
import { ReelView } from './reel-view';

function getMaskGraphics(config = {}) {
  const { alpha = 1, color = 0x0 } = config;
  const { mask: points } = store.game.slotMachine.config;

  const gr = CI_API.game.add.graphics();
  gr.beginFill(color, 1);
  gr.drawPolygon(points);
  gr.endFill();

  gr.alpha = alpha;

  return gr;
}

export class SlotMachineView extends Container {
  constructor() {
    super();

    this._bg = null;
    this._reels = null;
    this._reelsBg = null;
    this._reelsGroup = null;

    this._build();

    lego.event.on(ModelEvents.SlotMachineModel.StateUpdate, this._onStateUpdate, this);
    lego.event.on(ModelEvents.ReelModel.StateUpdate, this._onReelStateUpdate, this);
    lego.event.on(ViewEvents.EffectsView.SpinResultAnimationStart, this._onSpinResultAnimationStart, this);
    lego.event.on(ViewEvents.EffectsView.SpinResultAnimationComplete, this._onSpinResultAnimationComplete, this);
  }

  get name() {
    return 'SlotMachineView';
  }

  get reels() {
    return this._reels;
  }

  getBounds() {
    return this._bg.getBounds();
  }

  getReel(uuid) {
    return this._reels.find(reel => reel.uuid === uuid);
  }

  _onSpinResultAnimationStart() {
    this.game.add.tween(this._blocker).to({ alpha: 0.2 }, 200, null, true);
  }

  _onSpinResultAnimationComplete() {
    this.game.add.tween(this._blocker).to({ alpha: 0 }, 200, null, true);
  }

  _onStateUpdate(state) {
    this._switchInputs(false);

    switch (state) {
      case SlotMachineState.Idle:
        this._switchInputs(true);
        break;
      default:
    }
  }

  _onReelStateUpdate(newState, oldState, uuid) {
    switch (newState) {
      case ReelState.Spin:
        this.getReel(uuid).spin();
        break;
      case ReelState.MaxSpeed:
        // this.getReel(uuid).blur();
        break;
      case ReelState.Stop:
        this.getReel(uuid).stop();
        break;
      default:
    }
  }

  _switchInputs(value) {
    store.game.slotMachine.config.clickable && (this._bg.input.enabled = value);
  }

  _onBlockerClick() {
    lego.event.emit(ViewEvents.SlotMachineView.Spin);
  }

  // BUILD
  _build() {
    const { clickable } = store.game.slotMachine.config;

    this._buildBg();
    this._buildReels();
    this._buildBlocker();
    this._switchInputs(clickable);
  }

  _buildBg() {
    const bgPart = makeImage(getSlotMachineBgImageConfig());
    const { width, height } = bgPart;

    bgPart.scale.set(1 / 0.76);

    const rt = new Phaser.RenderTexture(this.game, width * 2, height);
    rt.renderXY(bgPart, 0, 0);
    bgPart.scale.x *= -1;
    rt.renderXY(bgPart, width * 2, 0);

    this._bg = this.game.add.sprite(0, 0, rt);
    this._bg.name = this.name;
    this._bg.inputEnabled = true;
    this._bg.events.onInputDown.add(this._onBlockerClick, this);

    this.addChild(this._bg);
  }

  _buildReels() {
    const { reels } = store.game.slotMachine;

    const group = new Container();
    this._reels = reels.map(model => {
      const { offset } = model.config;
      const reel = new ReelView(model);
      reel.position.set(group.width + offset.x, offset.y);

      return group.add(reel);
    });

    group.centerX = this._bg.centerX;
    group.y = this._bg.y + 20;

    const mask = getMaskGraphics({ alpha: 0.9 });
    mask.x = this._bg.left;
    mask.y = this._bg.top;

    this.add((group.mask = mask));
    this.add((this._reelsGroup = group));
  }

  _buildBlocker() {
    this._blocker = this.add(getMaskGraphics({ alpha: 0 }));
    this._blocker.x = this._bg.left;
    this._blocker.y = this._bg.top;
  }
}
