/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { getCoinImageConfig } from '../../configs/image-configs';
import { getBalanceBgNinePatchConfig } from '../../configs/ninepatch-configs';
import { getBalanceLabelTextConfig } from '../../configs/text-configs';
import { ModelEvents } from '../../events/model-events';
import { store } from '../../models/store';
import { makeImage, makeNinePatch, makeText, tweenNumber } from '../../utils';
import { Container } from '../../utils/container';

export class BalanceView extends Container {
  constructor() {
    super();
    this._build();

    lego.event.on(ModelEvents.PlayerModel.BalanceUpdate, this._onBalanceUpdate, this);
  }

  destroy() {
    this.game.tweens.remove(this._numberTween);

    super.destroy();
  }

  rebuild() {
    this._bg.scale.y = LP(-1, 1);
    this._label.fontSize = LP(28, 32);
    this._coin.x = this._label.left - 40;
  }

  _onBalanceUpdate(newValue, oldValue) {
    this._numberTween = tweenNumber({
      from: oldValue,
      to: newValue,
      duration: 1000,
      step: 1,
      context: this,
      onUpdate: value => this._setText(Math.floor(value))
    });
  }

  _build() {
    this.addChild((this._bg = this._buildBg()));
    this.addChild((this._coin = this._buildCoin()));
    this.addChild((this._label = this._buildLabel()));

    this.rebuild();
  }

  _buildBg() {
    const bg = makeNinePatch(getBalanceBgNinePatchConfig());
    return bg;
  }

  _buildCoin() {
    const coin = makeImage(getCoinImageConfig());
    coin.scale.set(0.6);
    coin.position.set(-136, 5);
    return coin;
  }

  _buildLabel() {
    const text = makeText(getBalanceLabelTextConfig(store.player.balance.toLocaleString()));
    text.anchor.set(0.5);

    return text;
  }

  _setText(n) {
    this._label.setText({
      text: n.toLocaleString(),
      ignoreLocalization: true,
      toString() {
        return this;
      }
    });
  }
}
