import { CellScale } from '@armathai/phaser2-grid';
import { getGameBounds } from '../../utils';

export function getRewardGridLandscapeConfig() {
  return {
    bounds: getGameBounds(),
    // debug: { color: 0x2fc900 },
    name: 'reward',
    cells: [
      {
        name: 'blocker',
        bounds: { x: 0, y: 0, width: 1, height: 1 },
        scale: CellScale.Fill
      },
      {
        name: 'popup',
        bounds: { x: 0, y: 0, width: 1, height: 1 }
      },
      {
        name: 'emitter',
        bounds: { x: 0, y: 0, width: 1, height: 1 },
        scale: CellScale.None
      }
    ]
  };
}

