/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { Phaser2Grid } from '@armathai/phaser2-grid';
import { getUIGridConfig } from '../../configs/grid-configs';
import { ModelEvents } from '../../events/model-events';
import { TutorialView } from '../foreground/tutorial-view';
import { BalanceView } from './balance-view';
import { ControlsView } from './controls-view';
import { PersistentCTAView } from './persistent-cta-view';

export class UIView extends Phaser2Grid {
  constructor() {
    super(CI_API.game);
    super.build(this.getGridConfig());

    lego.event
      .on(ModelEvents.AdModel.PersistentCtaUpdate, this._onPersistentCtaUpdate, this)
      .on(ModelEvents.GameModel.SlotMachineUpdate, this._onSlotMachineUpdate, this)
      .on(ModelEvents.Store.PlayerUpdate, this._onPlayerUpdate, this)
      .on(ModelEvents.AdModel.TutorialUpdate, this._onTutorialUpdate, this);
  }

  get name() {
    return 'UIView';
  }

  getGridConfig() {
    return getUIGridConfig();
  }

  rebuild(config) {
    this._balanceView && this._balanceView.rebuild();
    this._tutorialView && this._tutorialView.rebuild();

    super.rebuild(config);
  }

  // TUTORIAL
  _onTutorialUpdate(tutorial) {
    tutorial ? this._buildTutorial() : this._destroyTutorial();
  }

  _buildTutorial() {
    this._tutorialView = new TutorialView();
    this._tutorialView.onSequenceReady.add(this._onTutorialSequenceReady, this);
  }

  _onTutorialSequenceReady() {
    this.setChild('tutorial', this._tutorialView);
  }

  _destroyTutorial() {
    this._tutorialView.destroy();
  }

  // PERSISTENT
  _onPersistentCtaUpdate(persistentCta) {
    persistentCta ? this._buildPersistentCta() : this._destroyPersistentCta();
  }

  _buildPersistentCta() {
    this.setChild('p_cta', (this._persistentCtaView = new PersistentCTAView()));
  }

  _destroyPersistentCta() {
    this._persistentCtaView.destroy();
  }

  // BALANCE
  _onPlayerUpdate(player) {
    player ? this._buildBalance() : this._destroyBalance();
  }

  _buildBalance() {
    this._balanceView = new BalanceView();
    this.setChild('balance', this._balanceView);
  }

  _destroyBalance() {
    this._balanceView.destroy();
  }

  // CONTROLS
  _onSlotMachineUpdate(slotMachine) {
    slotMachine ? this._buildControls() : this._destroyControls();
  }

  _buildControls() {
    this._controlsView = new ControlsView();
    this.setChild('footer', this._controlsView);
  }

  _destroyControls() {
    this._controlsView.destroy();
  }
}
