/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { getWinLabelTextConfig, getWinTextConfig } from '../../configs/text-configs';
import { SlotMachineState } from '../../constants';
import { ModelEvents } from '../../events/model-events';
import { ViewEvents } from '../../events/view-events';
import { store } from '../../models/store';
import { fitText, makeText, tweenNumber } from '../../utils';
import { PairInfoComponent } from './pair-info-component';

export class WinView extends PairInfoComponent {
  constructor() {
    super({
      label: getWinLabelTextConfig(),
      text: getWinTextConfig()
    });

    this._money = null;
    this._label = null;

    this._build();

    lego.event.on(ModelEvents.SlotMachineModel.StateUpdate, this._onSlotMachineStateUpdate, this);
  }

  destroy() {
    this.game.tweens.remove(this._numberTween);

    super.destroy();
  }

  _onSlotMachineStateUpdate() {
    const { state, spinResult } = store.game.slotMachine;

    switch (state) {
      case SlotMachineState.Spin:
        this._setText(0);
        break;
      case SlotMachineState.Action:
        this._tweenText(spinResult.prize);
        break;
      default:
    }
  }

  _tweenText(to) {
    const from = 0;

    this._numberTween = tweenNumber({
      from,
      to,
      duration: 1000,
      step: 1,
      context: this,
      onStart: () => this._onPrizeTweenStart(),
      onComplete: () => this._onPrizeTweenComplete(),
      onUpdate: value => this._onPrizeTweenUpdate(value)
    });
  }

  _onPrizeTweenStart() {
    lego.event.emit(ViewEvents.WinView.ScoreTweenStart);
  }

  _onPrizeTweenComplete() {
    lego.event.emit(ViewEvents.WinView.ScoreTweenComplete);
  }

  _onPrizeTweenUpdate(value) {
    this._setText(Math.floor(value));
  }

  _build() {
    this.addChild((this._money = this._buildMoneyText()));
    this.addChild((this._label = this._buildLabel()));

    this._setText(0);
  }

  _buildMoneyText() {
    const money = makeText(getWinTextConfig());
    money.anchor.set(0.5);
    money.x = LP(25, 25);
    money.y = 2;

    return money;
  }

  _buildLabel() {
    const label = makeText(getWinLabelTextConfig());
    label.anchor.set(0.5);
    label.x = LP(-93, -108);

    return label;
  }

  _setText(n) {
    this._money.setText({
      text: `${n.toLocaleString()}`,
      ignoreLocalization: true,
      toString: function toString() {
        return this;
      }
    });

    fitText(this._money, 200);
  }
}
