/* eslint-disable class-methods-use-this */
import { getTutorialBgImageConfig } from '../../configs/ninepatch-configs';
import { fitText, makeNinePatch, makeText } from '../../utils';
import { Container } from '../../utils/container';

export class TutorialSequenceView extends Container {
  constructor(config) {
    super();

    this._showTween = null;
    this._hideTween = null;
    this._textConfigFn = config.text;
    this.addChild((this._bg = this._buildBg()));
    this.addChild((this._label = this._buildLabel()));

    this.alpha = 0;
    this.rebuild();
  }

  destroy() {
    this.game.tweens.remove(this._showTween);
    this.game.tweens.remove(this._hideTween);

    super.destroy();
  }

  rebuild() {
    const width = LP(700, 550);
    const height = LP(184, 184);
    this._bg.resize(width, height);
    this._label.setStyle(this._textConfigFn().style);
  }

  show() {
    this._showTween = this.game.add.tween(this).to({ alpha: 1 }, 300, Phaser.Easing.Linear.None, true);
    return this._showTween;
  }

  hide() {
    this._hideTween = this.game.add.tween(this).to({ alpha: 0 }, 300, Phaser.Easing.Linear.None, true);
    return this._hideTween;
  }

  _buildBg() {
    return makeNinePatch(getTutorialBgImageConfig());
  }

  _buildLabel() {
    const { width, height } = this._bg;

    const label = makeText(this._textConfigFn());
    label.anchor.set(0.5);
    fitText(label, width * 0.8, height * 0.8);

    return label;
  }
}
