import { SpinBtnState } from '../../../constants';
import { ObservableModel } from '../../observable-model';

export class SpinButtonModel extends ObservableModel {
  constructor() {
    super('SpinButtonModel');

    this._state = null;
    this._active = null;

    this.makeObservable();
  }

  get state() {
    return this._state;
  }

  set state(value) {
    this._state = value;
  }

  get active() {
    return this._active;
  }

  set active(value) {
    this._active = value;
  }

  initialize() {
    this._state = SpinBtnState.Spin;
    this._active = true;
  }
}
