import { lego } from '@armathai/lego';
import { ASSETS } from '../../constants';
import { ModelEvents } from '../../events/model-events';
import { getViewByProperty } from '../../utils';

export class HintView extends Phaser.Sprite {
  constructor() {
    super(CI_API.game, 0, 0, ASSETS, 'ui/hand.png');
    this.anchor.set(0.1, 0);
    this.rotation = Math.PI * -0.78;

    lego.event.on(ModelEvents.HintModel.VisibleUpdate, this._onHintVisibleUpdate, this);
    this._hide();
  }

  destroy() {
    lego.event.removeListenersOf(this);

    super.destroy();
  }

  _onHintVisibleUpdate(visible) {
    visible ? this._show() : this._hide();
  }

  _show() {
    const spinView = getViewByProperty('name', 'SpinView');
    const pos = this.parent.toLocal(
      spinView.parent.toGlobal(new Phaser.Point(spinView.centerX + 54, spinView.centerY - 10)),
      undefined
    );

    this.position.set(pos.x, pos.y);
    this.scale.set(1);
    this.visible = true;
    this.game.tweens.removeFrom(this.scale);
    this.game.add.tween(this.scale).to({ x: 0.8, y: 0.8 }, 500, Phaser.Easing.Cubic.Out, true, 0, -1, true);
  }

  _hide() {
    this.game.tweens.removeFrom(this.scale);
    this.visible = false;
  }
}
