import { CellAlign } from '@armathai/phaser2-grid';
import { getGameBounds } from '../../utils';

const getBalanceHeight = () => {
  return LP(Math.min(0.134, 86 / getGameBounds().height), Math.min(0.09, 100 / getGameBounds().height));
};

const getFooterHeight = () => {
  return LP(Math.min(0.19, 120 / getGameBounds().height), Math.min(0.158, 180 / getGameBounds().height));
};

export function getUIGridLandscapeConfig() {
  const footerH = getFooterHeight();
  const footerY = 1 - footerH;

  const balanceH = getBalanceHeight();
  const balanceY = 0;

  const pCtaH = 0.105;
  const pCtaY = footerY - pCtaH;

  return {
    // debug: { color: 0x4287f5 },
    name: 'ui',
    cells: [
      {
        name: 'balance',
        bounds: { x: 0, y: balanceY, width: 1, height: balanceH },
        align: CellAlign.CenterTop,
        offset: { y: -24 }
      },
      {
        name: 'footer',
        bounds: { x: 0, width: 1, y: footerY }
      },
      {
        name: 'p_cta',
        bounds: { x: 0, y: pCtaY, width: 1, height: pCtaH },
        padding: { height: 0.9 }
      },
      {
        name: 'tutorial',
        bounds: { x: 0.3, width: 0.4, y: footerY },
        padding: -0.03
      }
    ]
  };
}

export function getUIGridPortraitConfig() {
  const footerH = getFooterHeight();
  const footerY = 1 - footerH;

  const balanceH = getBalanceHeight();
  const balanceY = 1 - (footerH + balanceH);

  const pCtaH = 0.09;
  const pCtaY = balanceY - pCtaH;

  return {
    // debug: { color: 0x4287f5 },
    name: 'ui',
    cells: [
      {
        name: 'balance',
        bounds: { x: 0, y: balanceY, width: 1, height: balanceH },
        align: CellAlign.CenterBottom,
        offset: { y: 8 }
      },
      {
        name: 'footer',
        bounds: { x: 0, y: footerY, width: 1, height: footerH }
      },
      {
        name: 'p_cta',
        bounds: { x: 0, y: pCtaY, width: 1, height: pCtaH }
      },
      {
        name: 'tutorial',
        bounds: { x: 0, y: 0.19, width: 1, height: 0.13 },
        padding: -0.13
      }
    ]
  };
}
