import { lego } from '@armathai/lego';
import { store } from '../../../models/store';
import { setBalanceCommand } from '../../player/set-balance-command';

export function slotMachineStateActionCommand() {
  const { balance } = store.player;
  const { prize } = store.game.slotMachine.spinResult;

  lego.command.payload(balance + prize).execute(setBalanceCommand);
}
