/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { Phaser2Grid } from '@armathai/phaser2-grid';
import { getControlsGridConfig } from '../../configs/grid-configs';
import { getUiOrnamentImageConfig } from '../../configs/image-configs';
import { getWinBetBgNinePathConfig } from '../../configs/ninepatch-configs';
import { ModelEvents } from '../../events/model-events';
import { makeGradient, makeImage, makeNinePatch } from '../../utils';
import { Container } from '../../utils/container';
import { BetView } from './bet-view';
import { SpinView } from './spin-view';
import { WinView } from './win-view';

export class ControlsView extends Phaser2Grid {
  constructor() {
    super(CI_API.game);

    this._bg = null;
    this._spin = null;
    this._win = null;
    this._bet = null;

    this._build();

    lego.event.on(ModelEvents.GameModel.SpinBtnUpdate, this._onSpinBtnUpdate, this);
  }

  get name() {
    return 'ControlsView';
  }

  getGridConfig() {
    return getControlsGridConfig();
  }

  rebuild(config) {
    super.rebuild(config);

    const scale = LP(0.64, 1);
    this._ornamentL.scale.set(scale);
    this._ornamentR.scale.set(scale);
    this._ornamentL.scale.x *= -1;

    this._ornamentL.bottom = this._bg.bottom + 8;
    this._ornamentR.bottom = this._bg.bottom + 8;

    this._ornamentL.x = this._bg.left + 28;
    this._ornamentR.x = this._bg.right - 28;
  }

  _onSpinBtnUpdate(value) {
    value && this._buildSpin();
  }

  _build() {
    super.build(this.getGridConfig());

    this._buildBg();
    this._buildWinBet();
  }

  _buildBg() {
    const bg = makeGradient({
      width: 10,
      height: 170,
      horizontal: 0,
      vertical: 1,
      colors: [
        {
          percent: 0,
          color: '#606d80'
        },
        {
          percent: 0.01,
          color: '#323845'
        },
        {
          percent: 0.03,
          color: '#000513'
        },
        {
          percent: 0.49,
          color: '#011b3e'
        },
        {
          percent: 1,
          color: '#010817'
        }
      ]
    });

    const ornamentConfig = getUiOrnamentImageConfig();
    const ornamentL = makeImage(ornamentConfig);
    const ornamentR = makeImage(ornamentConfig);

    this.setChild('bg', (this._bg = bg));
    this.addChild((this._ornamentL = ornamentL));
    this.addChild((this._ornamentR = ornamentR));
  }

  _buildWinBet() {
    const cont = new Container();

    const winBetBg = makeNinePatch(getWinBetBgNinePathConfig());

    this._win = new WinView();
    this._bet = new BetView();

    cont.addChild(winBetBg);
    cont.addChild(this._win);
    cont.addChild(this._bet);

    this._win.y = -this._win.height / 1.5 + 3;
    this._bet.y = this._bet.height / 2 + 3;

    winBetBg.scale.set(0.95);

    this.setChild('win_bet', cont);
  }

  _buildSpin() {
    this.setChild('spin', (this._spin = new SpinView()));
  }
}
