import { lego } from '@armathai/lego';
import { store } from '../../models/store';
import { clamp } from '../../utils/number/clamp';
import { setBetCommand } from '../player/set-bet-command';

export function betButtonClickCommand(value) {
  const { game, player } = store;
  const { bet } = player;
  const { step, max, min } = game.slotMachine.config.bet;

  const clamped = clamp(bet + value * step, min, max);
  lego.command.payload(clamped).execute(setBetCommand);
}
