import { CellScale } from '@armathai/phaser2-grid';

export function getGameGridLandscapeConfig() {
  const offsetY = CI_API.Globals.PARAMS.cta_btn_persistent ? -56 : -42;

  return {
    // debug: { color: 0x2fc900 },
    name: 'game',
    cells: [
      {
        name: 'machine',
        bounds: { x: 0.15, y: 0, width: 0.7, height: 1 },
        offset: { y: offsetY },
        scale: CellScale.None
      }
    ]
  };
}

export function getGameGridPortraitConfig() {
  return {
    // debug: { color: 0x2fc900 },
    name: 'game',
    cells: [
      {
        name: 'machine',
        bounds: { x: 0, y: 0.32, width: 1, height: 0.36 },
        padding: -0.052
      }
    ]
  };
}
