/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { Phaser2Grid } from '@armathai/phaser2-grid';
import { getGameGridConfig } from '../../configs/grid-configs';
import { ModelEvents } from '../../events/model-events';
import { SlotMachineView } from './machine/slot-machine-view';

export class GameView extends Phaser2Grid {
  constructor() {
    super(CI_API.game);
    super.build(this.getGridConfig());

    this._machine = null;

    lego.event.on(ModelEvents.GameModel.SlotMachineUpdate, this._onMachineUpdate, this);
  }

  get name() {
    return 'GameView';
  }

  getGridConfig() {
    return getGameGridConfig();
  }

  // MACHINE
  _onMachineUpdate(slotMachine) {
    slotMachine ? this._buildMachine() : this._destroyMachine();
  }

  _buildMachine() {
    this.setChild('machine', (this._machine = new SlotMachineView()));
  }

  _destroyMachine() {
    this._machine.destroy();
  }
}
