/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { getBetMinusButtonConfig, getBetPlusButtonConfig } from '../../configs/button-configs';
import { getBetLabelTextConfig, getBetTextConfig } from '../../configs/text-configs';
import { SlotMachineState } from '../../constants';
import { ModelEvents } from '../../events/model-events';
import { ViewEvents } from '../../events/view-events';
import { store } from '../../models/store';
import { fitText, makeText } from '../../utils';
import { Button } from '../../utils/button/button';
import { PairInfoComponent } from './pair-info-component';

export class BetView extends PairInfoComponent {
  constructor() {
    super({
      label: getBetLabelTextConfig(),
      text: getBetTextConfig()
    });

    this._bet = null;
    this._label = null;
    this._plus = null;
    this._minus = null;

    this._build();

    lego.event.on(ModelEvents.PlayerModel.BetUpdate, this._onBetUpdate, this);
    lego.event.on(ModelEvents.SlotMachineModel.StateUpdate, this._onSlotMachineStateUpdate, this);
  }

  _onBetUpdate(value) {
    this._setBet(value);
  }

  _onSlotMachineStateUpdate(value) {
    value === SlotMachineState.Idle ? this._checkForButtonsState() : this._switchButtonsEnable(false);
  }

  _build() {
    this.addChild((this._plus = this._buildPlusBtn()));
    this.addChild((this._minus = this._buildMinusBtn()));
    this.addChild((this._bet = this._buildBetText()));
    this.addChild((this._label = this._buildLabel()));

    this._setBet(store.player.bet);
  }

  _buildLabel() {
    const label = makeText(getBetLabelTextConfig());
    label.anchor.set(0.5);
    label.x = LP(-95, -108);
    if (this._plus) {
      label.y = this._plus.centerY + 2;
    }
    return label;
  }

  _buildBetText() {
    const bet = makeText(getBetTextConfig());
    bet.anchor.set(0.5);
    bet.x = this._plus.left - (this._plus.left - this._minus.right) / 2;
    bet.y = this._plus.centerY + 3;
    return bet;
  }

  _buildPlusBtn() {
    const btn = new Button(getBetPlusButtonConfig());
    btn.onClick.add(this._onButtonClick, this, 0, 1);
    btn.x = LP(btn.width * 1.95 - 1, btn.width * 2.18);
    btn.y = -3;
    btn.scale.x = -1;

    return btn;
  }

  _buildMinusBtn() {
    const btn = new Button(getBetMinusButtonConfig());
    btn.onClick.add(this._onButtonClick, this, 0, -1);
    btn.x = LP(-btn.width + 6, -btn.width * 1.2);
    btn.y = -3;

    return btn;
  }

  _setBet(value) {
    this._checkForButtonsState();
    this._setText(`${value.toLocaleString()}`);
  }

  _setText(text) {
    this._bet.setText({
      text,
      ignoreLocalization: true,
      toString: function toString() {
        return this;
      }
    });

    fitText(this._bet, this._plus.left - this._minus.right);
  }

  _checkForButtonsState() {
    const { game, player } = store;
    const { bet } = player;
    const { max, min } = game.slotMachine.config.bet;

    this._minus.switchEnable(bet !== min);
    this._plus.switchEnable(bet !== max);
  }

  _switchButtonsEnable(enable) {
    this._minus.switchEnable(enable);
    this._plus.switchEnable(enable);
  }

  _onButtonClick(target, value) {
    lego.event.emit(ViewEvents.BetView.ButtonClick, value);
  }
}
