import { CellAlign, CellScale } from '@armathai/phaser2-grid';

export function getEffectsGridLandscapeConfig() {
  return {
    // debug: { color: 0x4bdbd4 },
    name: 'effects',
    cells: [
      {
        name: 'result',
        bounds: { x: 0, y: 0, width: 1, height: 1 },
        align: CellAlign.LeftTop,
        scale: CellScale.None
      }
    ]
  };
}

