/* eslint-disable max-classes-per-file */
import { fitText, makeImage, makeNinePatch, makeText } from '..';
import { AbstractButton } from './abstract-button';

export class Button extends AbstractButton {
  createState(config) {
    const state = new Phaser.Group(this.game);

    const { bg, label } = config;

    // FRAME
    const bgObj = bg.width && bg.height ? makeNinePatch(bg) : makeImage(bg);
    state.add(bgObj);

    // LABEL
    if (label) {
      const labelObj = makeText(label);
      labelObj.anchor.set(0.5);
      state.add(labelObj);

      fitText(labelObj, bgObj.width * 0.8, bgObj.height * 0.8);
    }

    return this.add(state);
  }
}

export class FreeSpinButton extends AbstractButton {
  setText(value) {
    // @ts-ignore
    this.states.up.countLabel.setText(value);
    // @ts-ignore
    this.states.down.countLabel.setText(value);
    // @ts-ignore
    this.states.disable.countLabel.setText(value);
  }

  createState(config) {
    const state = new Phaser.Group(this.game);

    const { bg, label, count } = config;

    // FRAME
    const bgObj = bg.width && bg.height ? makeNinePatch(bg) : makeImage(bg);
    bgObj.anchor.set(0.5);

    const labelObj = makeText(label);
    labelObj.anchor.set(0.5);
    labelObj.y += 15;

    const countObj = makeText(count);
    countObj.anchor.set(0.5);
    countObj.y -= 9;

    state.add(bgObj);
    state.add(labelObj);
    state.add(countObj);

    // @ts-ignore
    state.countLabel = countObj;

    fitText(labelObj, bgObj.width * 0.9, bgObj.height * 0.9);

    return this.add(state);
  }
}
