/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { getLogoGlowConfig, getLogoImageConfig } from '../../configs/image-configs';
import { AdStatus } from '../../constants';
import { ModelEvents } from '../../events/model-events';
import { makeImage } from '../../utils';
import { Container } from '../../utils/container';

export class LogoView extends Container {
  constructor() {
    super();

    this._glow = makeImage(getLogoGlowConfig());
    this._glow.scale.set(2.4, 2);
    this._glow.alpha = 0;

    this._logo = makeImage(getLogoImageConfig());

    this.addChild(this._glow);
    this.addChild(this._logo);
    lego.event.on(ModelEvents.AdModel.StatusUpdate, this._onAdStatusUpdate, this);
  }

  get name() {
    return 'LogoView';
  }

  get logo() {
    return this._logo;
  }

  getBounds() {
    return this._logo.getBounds();
  }

  _highlight() {
    this.game.add.tween(this._glow).to({ alpha: 1 }, 250, Phaser.Easing.Linear.None, true);
  }

  _onAdStatusUpdate(status) {
    switch (status) {
      case AdStatus.Cta:
        this._highlight();
        break;
      default:
    }
  }
}
