import { lego, not } from '@armathai/lego';
import { GameStatus } from '../../../constants';
import { firstExtraSpinGuard } from '../../../guards/game/first-extra-spin-guard';
import { spinsToCtaGuard } from '../../../guards/game/spins-to-cta-guard';
import { itemsToCtaCompleteCommand } from '../../ad/cta/items-to-cta-complete-command';
import { idleSlotMachineCommand } from '../../game/machine/idle-slot-machine-command';
import { setGameStatusCommand } from '../../game/set-game-status-command';

export function effectsViewSpinResultAnimationCompleteCommand() {
  lego.command
    .guard(spinsToCtaGuard)
    .execute(itemsToCtaCompleteCommand)

    .payload(GameStatus.Reward)
    .guard(firstExtraSpinGuard)
    .execute(setGameStatusCommand)

    .guard(not(spinsToCtaGuard), not(firstExtraSpinGuard))
    .execute(idleSlotMachineCommand);
}
