import { lego } from '@armathai/lego';
import { tutorialModelGuard } from '../../../guards/ad/tutorial-model-guard';
import { updateTutorialCommand } from '../../ad/tutorial/update-tutorial-command';
import { updateAutoSpinCommand } from '../update-auto-spin-command';
import { updateSpinButtonCommand } from '../update-spin-button-command';

export function slotMachineStateIdleCommand() {
  lego.command
    .execute(updateAutoSpinCommand)
    .execute(updateSpinButtonCommand)

    .guard(tutorialModelGuard)
    .execute(updateTutorialCommand);
}
