import { ReelState } from '../../../constants';
import { ObservableModel } from '../../observable-model';
import { SlotModel } from './slot-model';

function extendConfig(config) {
  const { offset } = config;
  config.offset = { x: offset.x || 0, y: offset.y || 0 };

  return config;
}

export class ReelModel extends ObservableModel {
  constructor(config) {
    super('ReelModel');

    this._config = extendConfig(config);
    this._slots = this._generateSlots();
    this._state = ReelState.Idle;

    this.makeObservable('_state');
  }

  get config() {
    return this._config;
  }

  get slots() {
    return this._slots;
  }

  get state() {
    return this._state;
  }

  getSlot(uuid) {
    return this._slots.find(slot => slot.uuid === uuid);
  }

  setState(value) {
    this._state = value;
  }

  setSlotState(slotIndex, state) {
    const slot = this._slots[slotIndex];
    slot.setState(state);
  }

  setSlotType(slotIndex, type) {
    const slot = this._slots[slotIndex];
    slot.setType(type);
  }

  _generateSlots() {
    return this._config.slots.map(slotType => new SlotModel(slotType));
  }
}
