import { lego } from '@armathai/lego';
import { firstExtraSpinGuard } from '../../../guards/game/first-extra-spin-guard';
import { firstSpinGuard } from '../../../guards/game/first-spin-guard';
import { nextTutorialSequenceCommand } from './next-tutorial-sequence-command';

export function updateTutorialCommand() {
  lego.command
    .guard(firstSpinGuard)
    .execute(nextTutorialSequenceCommand)

    .guard(firstExtraSpinGuard)
    .execute(nextTutorialSequenceCommand);
}
