import { lego } from '@armathai/lego';
import { AdStatus, GameStatus } from '../constants';
import { store } from '../models/store';
import { mapAdStatusUpdateCommand } from './ad/map-ad-status-update-command';
import { setAdStatusCommand } from './ad/set-ad-status-command';
import { idleSlotMachineCommand } from './game/machine/idle-slot-machine-command';
import { setGameStatusCommand } from './game/set-game-status-command';
import { setBetCommand } from './player/set-bet-command';

export function gameStartCommand() {
  lego.command
    .execute(mapAdStatusUpdateCommand)

    .payload(AdStatus.Game)
    .execute(setAdStatusCommand)

    .payload(GameStatus.Slot)
    .execute(setGameStatusCommand)

    .payload(store.game.slotMachine.config.bet.min)
    .execute(setBetCommand)

    .execute(idleSlotMachineCommand);
}
