import { ObservableModel } from '../observable-model';

export class PlayerModel extends ObservableModel {
  constructor() {
    super('PlayerModel');

    this._balance = 100000000;
    this._balanceOnStart = this._balance;
    this._bet = 0;
    this.makeObservable();
  }

  get profit() {
    return this.balance - this._balanceOnStart;
  }

  get balance() {
    return this._balance;
  }

  set balance(value) {
    this._balance = value;
  }

  get bet() {
    return this._bet;
  }

  set bet(value) {
    this._bet = value;
  }
}
