import { getWinCoinEmitterConfig } from '../../configs/particles-configs';
import { SlotSpineMap } from '../../configs/spine-configs';
import { store } from '../../models/store';
import { delayRunnable, getViewByProperty, makeSpine, removeRunnable } from '../../utils';
import { Container } from '../../utils/container';
import { CoinFallEmitter } from '../../utils/emitters/coin-fall/coin-fall-emitter';

export class WinBehaviour extends Container {
  constructor() {
    super();

    this._slots = null;
    this._completeRunnable = null;

    this.onComplete = new Phaser.Signal();
  }

  destroy() {
    super.destroy();

    removeRunnable(this._completeRunnable);
  }

  show() {
    this._buildSlots();
    this.resize();

    if (!store.game.slotMachine.isLastSpin()) {
      this._buildParticles();
    }

    this.visible = true;

    this._completeRunnable = delayRunnable(2400, this.onComplete.dispatch, this.onComplete);
  }

  hide() {
    this.game.add
      .tween(this)
      .to({ alpha: 0 }, 600, Phaser.Easing.Cubic.Out, true)
      .onComplete.addOnce(() => {
        this.destroy();
      });
  }

  resize() {
    this._slots.forEach(s => {
      const { slotUUID } = s;
      const slotView = getViewByProperty('uuid', slotUUID);
      const scale = slotView.worldScale.x / this.worldScale.x;
      s.scale.set(1);
      s.scale.set(scale);

      const pos = this.toLocal(slotView.position, slotView.parent);
      s.position.set(pos.x, pos.y);
    });
  }

  _buildSlots() {
    this._slots = [];

    const sloMachineModel = store.game.slotMachine;
    // const sloMachineView = getViewByUUID('SlotMachineView');

    const { reels } = sloMachineModel.spinResult;
    reels.forEach((r, i) => {
      const reelModel = sloMachineModel.reels[i];

      r.forEach(s => {
        const slotModel = reelModel.slots[s.index];

        this._buildSlot(slotModel.uuid);
      });
    });
  }

  _buildParticles() {
    this._emitter = new CoinFallEmitter(getWinCoinEmitterConfig());
    this._emitter.emit();
    this.addChild(this._emitter);
  }

  _buildSlot(slotUUID) {
    const slotCont = new Container();

    const slotView = getViewByProperty('uuid', slotUUID);
    slotView.visible = false;
    const { type } = slotView;

    const slot = makeSpine({ key: SlotSpineMap[type] });
    slot.setToSetupPose();
    slot.setAnimationByName(0, 'animation', false);
    slot.onComplete.addOnce(() => {
      slotView.visible = true;
    });

    const frame = makeSpine({ key: 'frame' });
    frame.setAnimationByName(0, 'start', false);
    frame.onComplete.addOnce(() => {
      frame.setAnimationByName(0, 'idle', true);
    });

    // @ts-ignore
    slotCont.slotUUID = slotUUID;

    slotCont.addChild(slot);
    slotCont.addChild(frame);

    this.addChild(slotCont);
    this._slots.push(slotCont);
  }
}
