import { loopRunnable, makeImage, removeRunnable } from '../..';
import { sample } from '../../array/sample';
import { Container } from '../../container';
import { randomReal } from '../../number/random-real';

export class SparkleEmitter extends Container {
  emit() {
    this.stop();
    this._area = new Phaser.Circle(0, 0, 800);
    this._spawnRunnable = loopRunnable(20, this._spawn, this);
  }

  stop() {
    removeRunnable(this._spawnRunnable);
    this.removeChildren();
  }

  _spawn() {
    const frame = sample(['circle', 'sparkle']);
    const particle = makeImage({ key: `effects/${frame}.png` });

    const angle = Math.random() * Math.PI * 2;

    const toX = Math.cos(angle) * this._area.radius;
    const toY = Math.sin(angle) * this._area.radius;

    const rnd = randomReal(0.3, 0.7);
    const fromX = toX * rnd;
    const fromY = toY * rnd;
    const pos = new Phaser.Rectangle(fromX, fromY);
    const scale = randomReal(0.3, 1);
    particle.position.set(pos.x, pos.y);
    particle.alpha = 0;

    const t1 = this.game.add.tween(particle).to({ rotation: 2 * Math.PI }, 5000, null, true, 0, -1);
    const t2 = this.game.add.tween(particle).to({ alpha: 1 }, 800, Phaser.Easing.Linear.None, true, 0, 0, true);
    const t3 = this.game.add.tween(particle.scale).to({ x: scale, y: scale }, 800, null, true, 0, 0, true);
    const t4 = this.game.add.tween(particle).to({ x: toX, y: toY }, 1600, null, true, 0, 0, false);

    t4.onComplete.addOnce(() => {
      particle.destroy();
    });

    // @ts-ignore
    t1.universal = true;
    // @ts-ignore
    t2.universal = true;
    // @ts-ignore
    t3.universal = true;
    // @ts-ignore
    t4.universal = true;

    this.addChild(particle);
  }
}
