import { CellScale } from '@armathai/phaser2-grid';

export function getCTAStyle1GridLandscapeConfig() {
  return {
    // debug: { color: 0x4bdbd4 },
    name: 'cta',
    cells: [
      {
        name: 'popup',
        bounds: { x: 0, y: 0.36, width: 1, height: 0.33 }
      },
      {
        name: 'play_button',
        bounds: { x: 0 }
      },
      {
        bounds: { x: 0, y: 0, width: 1, height: 1 },
        name: 'blocker',
        scale: CellScale.Fill
      }
    ]
  };
}

