import { makeText } from '../../utils';
import { Container } from '../../utils/container';

export class PairInfoComponent extends Container {
  constructor(config) {
    super();

    const { label, text } = config;
    this._buildLabel(label);
    this._buildText(text);
  }

  _buildLabel(labelConfig) {
    const label = makeText(labelConfig);

    this.addChild(label);
  }

  _buildText(textConfig) {
    const text = makeText(textConfig);
    text.anchor.set(0.5);
    return this.addChild(text);
  }
}
