import { ReelState, SlotMachineState, SpinResultType } from '../../../constants';
import { last } from '../../../utils/array/last';
import { sample } from '../../../utils/array/sample';
import { shuffle } from '../../../utils/array/shuffle';
import { delayRunnable, removeRunnable } from '../../../utils/index';
import { ObservableModel } from '../../observable-model';
import { ReelModel } from './reel-model';

export class SlotMachineModel extends ObservableModel {
  constructor(config) {
    super('SlotMachineModel');

    this._config = config;
    this._reels = this._generateReels();
    this._state = null;
    this._spinResult = null;
    this._spinsToCta = 0;
    this._spinsCount = 0;
    this._autoSpinTimer = null;

    this.makeObservable();
  }

  destroy() {
    this.stopAutoSpinTimer();
  }

  get config() {
    return this._config;
  }

  get reels() {
    return this._reels;
  }

  get state() {
    return this._state;
  }

  get spinResult() {
    return this._spinResult;
  }

  get spinsCount() {
    return this._spinsCount;
  }

  get spinsToCta() {
    return this._spinsToCta;
  }

  set spinsToCta(value) {
    this._spinsToCta = value;
  }

  isLastSpin() {
    return this.spinsToCta === this.spinsCount;
  }

  isLastReel(uuid) {
    return last(this._reels).uuid === uuid;
  }

  getReelIndex(uuid) {
    return this._reels.indexOf(this._reels.find(r => r.uuid === uuid));
  }

  getReel(uuid) {
    return this._reels.find(r => r.uuid === uuid);
  }

  setState(value) {
    this._state = value;
  }

  setSpinsCount(value) {
    this._spinsCount = value;
  }

  spin() {
    this.setState(SlotMachineState.Spin);

    this._reels.forEach((r, index) =>
      delayRunnable(index * this._config.reelsSpinDelay, r.setState, r, ReelState.Spin)
    );

    this.stopAutoSpinTimer();
    this.setSpinsCount(this._spinsCount + 1);
    this._setSpinResult = null;
  }

  stop() {
    this.setState(SlotMachineState.Stop);

    this._reels.forEach((r, index) =>
      delayRunnable(index * this._config.reelsStopDelay, r.setState, r, ReelState.Stop)
    );
  }

  idle() {
    this.setState(SlotMachineState.Idle);
  }

  resetSlots(shuffleAfterReset) {
    const { reels } = this.config;

    reels.forEach((r, i) => {
      const { slots } = r;

      const reelModel = this._reels[i];
      const slotTypes = [...slots];
      shuffleAfterReset && shuffle(slotTypes);
      slotTypes.forEach((s, j) => {
        reelModel.slots[j].type = s;
      });
    });
  }

  startAutoSpinTimer() {
    // @ts-ignore
    this._autoSpinTimer = delayRunnable(CI_API.Globals.PARAMS.auto_spin_delay || 0, this.spin, this);
  }

  stopAutoSpinTimer() {
    removeRunnable(this._autoSpinTimer);
  }

  // SPIN RESULT
  setSpinResult(bet) {
    this._spinResult = this._generateSpinResult(bet);
    this._spinResult.reels.forEach((r, i) => r.forEach(s => this._reels[i].setSlotType(s.index, s.type)));
  }

  getWinType() {
    const { first_slot_spins } = CI_API.Globals.PARAMS;

    if (this.isLastSpin()) {
      return SpinResultType.Mega;
    }

    if (this.spinsCount === first_slot_spins) {
      return SpinResultType.Big;
    }

    return SpinResultType.Regular;
  }

  getResultPattern(winType) {
    return sample(this._config.combinations[winType]);
  }

  _getReelsResultPattern(pattern) {
    const slotTypes = this.getWinSlotTypes();

    return pattern.map(reelPattern => {
      return reelPattern.map(slotIndex => {
        return { index: slotIndex, type: sample(slotTypes) };
      });
    });
  }

  getWinSlotTypes() {
    const { winSlotTypes } = this.config;
    const { first_slot_spins } = CI_API.Globals.PARAMS;

    if (this.isLastSpin()) {
      return [sample(winSlotTypes.mega)];
    }

    if (this._spinsCount === first_slot_spins) {
      return [sample(winSlotTypes.big)];
    }

    const reg = sample(winSlotTypes.regular);
    const mega = sample(winSlotTypes.mega);
    return [reg, reg, mega, reg, reg];
  }

  getPrize(winType, bet) {
    let prize = winType === SpinResultType.Loose ? 0 : this._config.prizeFactor[winType] * bet;

    if (this.isLastSpin()) {
      prize = 999999999;
    }

    return prize;
  }

  _generateSpinResult(bet) {
    const type = this.getWinType();
    const prize = this.getPrize(type, bet);
    const pattern = this.getResultPattern(type);
    const reels = this._getReelsResultPattern(pattern);

    return { type, prize, pattern, reels };
  }

  _generateReels() {
    return this._config.reels.map(reelConfig => new ReelModel(reelConfig));
  }
}
