import { lego, not } from '@armathai/lego';
import { ReelState, SlotMachineState } from '../../../constants';
import { lastReelGuard } from '../../../guards/game/last-reel-guard';
import { stopButtonGuard } from '../../../guards/game/stop-button-guard';
import { setReelStateCommand } from '../../game/machine/reel/set-reel-state-command';
import { setSlotMachineStateCommand } from '../../game/machine/set-slot-machine-state-command';
import { resetSlotsCommand } from '../../game/machine/slot/reset-slot-command';
import { stopSlotMachineCommand } from '../../game/machine/stop-slot-machine-command';

export function reelSpeedUpCompleteCommand(uuid) {
  lego.command
    .payload(true)
    .execute(resetSlotsCommand)

    .payload(uuid, ReelState.MaxSpeed)
    .execute(setReelStateCommand)

    .payload(SlotMachineState.MaxSpeed)
    .guard(() => lastReelGuard(uuid))
    .execute(setSlotMachineStateCommand)

    .payload(uuid)
    .guard(() => lastReelGuard(uuid), not(stopButtonGuard))
    .execute(stopSlotMachineCommand);
}
