import { getSlotMachineConfig } from '../../configs/slot-machine-config';
import { GameStatus } from '../../constants';
import { ObservableModel } from '../observable-model';
import { SlotMachineModel } from './machine/slot-machine-model';
import { SpinButtonModel } from './machine/spin-button-model';

export class GameModel extends ObservableModel {
  constructor() {
    super('GameModel');

    this._slotMachine = null;
    this._spinBtn = null;
    this._status = GameStatus.Unknown;

    this.makeObservable();
  }

  get slotMachine() {
    return this._slotMachine;
  }

  get spinBtn() {
    return this._spinBtn;
  }

  get status() {
    return this._status;
  }

  set status(value) {
    this._status = value;
  }

  initialize() {
    this.initializeMachineModel();
  }

  destroy() {
    this.destroyMachineModel();
  }

  initializeMachineModel() {
    this._slotMachine = new SlotMachineModel(getSlotMachineConfig());
    this._slotMachine.initialize();

    this._spinBtn = new SpinButtonModel();
    this._spinBtn.initialize();
  }

  destroyMachineModel() {
    this._slotMachine.destroy();
    this._slotMachine = null;
  }
}
