/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { Phaser2Grid } from '@armathai/phaser2-grid';
import { getCtaCoinEmitterConfig } from '../../configs/particles-configs';
import { AdStatus, InputPriority } from '../../constants';
import { ModelEvents } from '../../events/model-events';
import { ViewEvents } from '../../events/view-events';
import moduleBindings from '../../module-bindings';
import { makePixel } from '../../utils';
import { Button } from '../../utils/button/button';
import { CoinFallEmitter } from '../../utils/emitters/coin-fall/coin-fall-emitter';

export class CTAView extends Phaser2Grid {
  constructor() {
    super(CI_API.game);

    this._blocker = null;
    this._emitter = null;
    this.bindings = moduleBindings;
    lego.event.on(ModelEvents.AdModel.StatusUpdate, this._onAdStatusUpdate, this);
  }

  getGridConfig() {
    return new Error('function getGridConfig() is not implemented');
  }

  postRebuild() {
    this.show();
  }

  show() {
    const { Linear } = Phaser.Easing;

    const tween = this.game.add.tween(this._blocker).from({ alpha: 0 }, 200, Linear.None, true);
    tween.onStart.addOnce(() => {
      this._blocker.visible = true;
    });
    // @ts-ignore
    tween.universal = true;
  }

  rebuild(config) {
    if (!this.grid) {
      return;
    }

    super.rebuild(config);

    this._emitter.destroy();
    this._buildEmitter();
  }

  buildPlayButton(config) {
    return this._buildButton(config, this._onPlayClick);
  }

  buildRetryButton(config) {
    return this._buildButton(config, this._onRetryClick);
  }

  build(config, blockerAlpha = 0.7) {
    super.build(config);
    this._buildBlocker(blockerAlpha);
    this._buildEmitter();
  }

  _onAdStatusUpdate(status) {
    switch (status) {
      case AdStatus.Cta:
        this.build();
        break;
      case AdStatus.Game:
        this.removeChildren();
        break;
      default:
    }
  }

  _buildBlocker(alpha) {
    this._blocker = makePixel({ alpha });
    this._blocker.inputEnabled = true;
    this._blocker.input.priorityID = InputPriority.Cta;
    this._blocker.name = this.name;
    this._blocker.visible = false;

    this.setChild('blocker', this._blocker);

    if (CI_API.Globals.PARAMS.cta_scrn_clickable) {
      this._blocker.events.onInputDown.add(this._onScreenClick, this);
    }
  }

  _buildEmitter() {
    this._emitter = new CoinFallEmitter(getCtaCoinEmitterConfig());
    this._emitter.emit();
    this.addChildAt(this._emitter, 1);
  }

  _buildButton(config, callback) {
    const btn = new Button(config);
    btn.onClick.add(callback, this);
    return btn;
  }

  _onScreenClick() {
    lego.event.emit(ViewEvents.CtaView.ScreenClick);
  }

  _onPlayClick() {
    lego.event.emit(ViewEvents.CtaView.PlayClick);
  }

  _onRetryClick() {
    lego.event.emit(ViewEvents.CtaView.RetryClick);
  }
}
