import { ASSETS } from '../../../constants';
import { store } from '../../../models/store';
import { makeImage } from '../../../utils';
import { Container } from '../../../utils/container';

export class SlotView extends Container {
  constructor(model) {
    super();

    this.name = 'SlotView';

    const { uuid, type } = model;
    this._uuid = uuid;
    this._type = type;
    this._width = null;
    this._height = null;
    this._blur = false;

    this._updateDimensions();

    this._build();
  }

  get uuid() {
    return this._uuid;
  }

  get type() {
    return this._type;
  }

  get width() {
    return this._width;
  }

  get height() {
    return this._height;
  }

  get top() {
    return this.y - this.height * this._slot.anchor.y;
  }

  get bottom() {
    return this.y + this.height * this._slot.anchor.y;
  }

  setY(value) {
    this.y = value + this.height * this._slot.anchor.y;
  }

  setType(value) {
    this._type = value;

    this._buildSlot();
    this._updateDimensions();
  }

  blur() {
    this._blur = true;
    this._slot.loadTexture(ASSETS, `slot/slot-blur-${this._type + 1}.png`);
  }

  unBlur() {
    this._blur = false;
    this._slot.loadTexture(ASSETS, `slot/slot-${this._type + 1}.png`);
  }

  highlightSpinResult() {
    this.game.add
      .tween(this.scale)
      .to({ x: '-0.1', y: '-0.1' }, 600, Phaser.Easing.Quadratic.InOut, true, 800, 0, true);
  }

  _build() {
    this._buildSlot();
  }

  _buildSlot() {
    this._slot && this._slot.destroy();

    const slot = makeImage({ frame: `slot/slot-${this._blur ? 'blur-' : ''}${this._type + 1}.png` });
    slot.anchor.set(0.5);

    this.addChild((this._slot = slot));
  }

  _updateDimensions() {
    const { slotDimensions } = store.game.slotMachine.config;
    this._width = slotDimensions[this._type].width;
    this._height = slotDimensions[this._type].height;
  }
}
