/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { Phaser2Grid } from '@armathai/phaser2-grid';
import { getEffectsGridConfig } from '../../configs/grid-configs';
import { SlotMachineState, SpinResultType } from '../../constants';
import { ModelEvents } from '../../events/model-events';
import { ViewEvents } from '../../events/view-events';
import { store } from '../../models/store';
import { delayRunnable, removeRunnable } from '../../utils';
import { BigWinBehaviour } from './big-win-behaviour';
import { MegaWinBehaviour } from './mega-win-behaviour';
import { RegularWinBehaviour } from './regular-win-behaviour';

export class EffectsView extends Phaser2Grid {
  constructor() {
    super(CI_API.game);
    super.build(this.getGridConfig());

    this._winBehaviour = null;
    this._completeRunnable = null;

    lego.event.on(ModelEvents.SlotMachineModel.StateUpdate, this._onSlotMachineStateUpdate, this);
    lego.event.on(ModelEvents.CtaModel.VisibleUpdate, this._onCtaVisibleUpdate, this);
  }

  resize() {
    this._winBehaviour && this._winBehaviour.resize();
  }

  destroy() {
    removeRunnable(this._completeRunnable);
    super.destroy();
  }

  getGridConfig() {
    return getEffectsGridConfig();
  }

  _onCtaVisibleUpdate(value) {
    value && this._winBehaviour && this._winBehaviour.destroy();
  }

  _onSlotMachineStateUpdate() {
    const { state, spinResult } = store.game.slotMachine;

    switch (state) {
      case SlotMachineState.Action:
        switch (spinResult.type) {
          case SpinResultType.Regular:
            this._setWinBehaviour(RegularWinBehaviour);
            break;
          case SpinResultType.Big:
            this._setWinBehaviour(BigWinBehaviour);
            this.game.camera.shake(0.006, 400);
            break;
          case SpinResultType.Mega:
            this._setWinBehaviour(MegaWinBehaviour);
            break;
          default:
            this._setCompleteTimer(0);
        }
        break;
      default:
        this._winBehaviour = null;
    }
  }

  _setCompleteTimer(duration) {
    this._completeRunnable = delayRunnable(duration, () => {
      this._onAnimationComplete();
    });
  }

  _setWinBehaviour(behaviour) {
    // eslint-disable-next-line new-cap
    this.setChild('result', (this._winBehaviour = new behaviour()));
    this._winBehaviour.onComplete.addOnce(this._setCompleteTimer, this, 0, 0);
    this._winBehaviour.show();

    lego.event.emit(ViewEvents.EffectsView.SpinResultAnimationStart);
  }

  // eslint-disable-next-line class-methods-use-this
  _onAnimationComplete() {
    this._winBehaviour && this._winBehaviour.hide();

    lego.event.emit(ViewEvents.EffectsView.SpinResultAnimationComplete);
  }
}
