/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { Phaser2Grid } from '@armathai/phaser2-grid';
import { getForegroundGridConfig } from '../../configs/grid-configs';
import { AdStatus } from '../../constants';
import { ModelEvents } from '../../events/model-events';
import { HintView } from './hint-view';
import { LogoView } from './logo-view';
import { RewardView } from './reward/reward-view';
import { SoundView } from './sound-view';

export class ForegroundView extends Phaser2Grid {
  constructor() {
    super(CI_API.game);
    super.build(this.getGridConfig());

    lego.event
      .on(ModelEvents.AdModel.StatusUpdate, this._onAdStatusUpdate, this)
      .on(ModelEvents.SoundModel.IconUpdate, this._onSoundIconUpdate, this)
      .on(ModelEvents.AdModel.HintUpdate, this._onHintUpdate, this);
  }

  get name() {
    return 'ForegroundView';
  }

  getGridConfig() {
    return getForegroundGridConfig();
  }

  _onAdStatusUpdate(status) {
    switch (status) {
      case AdStatus.Game:
        this._build();
        break;
      case AdStatus.Cta:
        this._adjustCtaLogo();
        break;
      default:
    }
  }

  _onSoundIconUpdate(value) {
    value && this._buildSoundIcon();
  }

  _build() {
    this._buildLogo();
    this._buildReward();
  }

  // LOGO
  _buildLogo() {
    this.setChild('logo', (this._logoView = new LogoView()));
  }

  // REWARD
  _buildReward() {
    this.setChild('foreground', (this._rewardView = new RewardView()));
  }

  // SOUND
  _buildSoundIcon() {
    this.setChild('sound', (this._soundView = new SoundView()));
  }

  // HINT
  _onHintUpdate(hint) {
    hint ? this._buildHint() : this._destroyHint();
  }

  _buildHint() {
    this.addChild((this._hintView = new HintView()));
  }

  _destroyHint() {
    this._hintView.destroy();
  }

  _adjustCtaLogo() {
    const { Sinusoidal } = Phaser.Easing;

    const cell = this.getCellByName('cta_logo').area;
    const pos = new Phaser.Point(cell.x + cell.width / 2, cell.y + cell.height / 2);
    const maxScale = Math.min(cell.width / this._logoView.logo.width, cell.height / this._logoView.logo.height);
    const scale = Math.min(1, maxScale);

    this.game.add.tween(this._logoView).to({ x: pos.x, y: pos.y }, 400, Sinusoidal.InOut, true);
    this.game.add
      .tween(this._logoView.scale)
      .to({ x: scale, y: scale }, 400, Sinusoidal.InOut, true)
      .onComplete.addOnce(() => {
        this.rebuildChild(this._logoView, 'cta_logo');
      });
  }
}
