import { lego } from '@armathai/lego';
import { GameStatus, SlotMachineState, SpinResultType } from '../constants';
import { ModelEvents } from '../events/model-events';
import { ViewEvents } from '../events/view-events';
import { store } from '../models/store';

let sound;

function play(id, volume) {
  sound.play(id, volume);
}

function stop(id) {
  sound.stop(id);
}

function onLoadCompleteUpdate(complete) {
  if (!complete) {
    return;
  }
  sound = soundLoader.addAudioSprite('audio');
}

function playGameTheme() {
  play('game-theme', 1);
}

function playCount() {
  play('count', 1);
}

function stopCount() {
  stop('count');
}

function playCleopatraSlot() {
  play('cleopatra', 1);
}

function playCaesarSlot() {
  play('caesar', 1);
}

function playClick() {
  play('click', 1);
}

function onWin(spinResult) {
  const { type, reels } = spinResult;
  if (type === 'mega') {
    const winSlotType = reels[0][0].type;

    switch (winSlotType) {
      case 8:
        playCleopatraSlot();
        break;
      case 9:
        playCaesarSlot();
        break;
      default:
    }
  } else {
    play('win', 1);
  }
}

function gameStatusUpdate(status) {
  switch (status) {
    case GameStatus.Reward:
      play('reward', 1);
      break;
    default:
  }
}

function onMachineStateUpdate(state) {
  const { spinResult } = store.game.slotMachine;

  switch (state) {
    case SlotMachineState.Spin:
      play('reel-tick', 2);
      break;
    case SlotMachineState.Action:
      stop('reel-tick');

      switch (spinResult.type) {
        case SpinResultType.Loose:
          break;
        default:
          onWin(spinResult);
      }

      break;
    default:
  }
}

export function SoundObservant() {
  lego.event
    .on(ModelEvents.LoadModel.CompleteUpdate, onLoadCompleteUpdate, this)
    .once(ViewEvents.Game.UserInteraction, playGameTheme, this)
    .on(ModelEvents.GameModel.StatusUpdate, gameStatusUpdate, this)
    .on(ModelEvents.SlotMachineModel.StateUpdate, onMachineStateUpdate, this)
    .on(ViewEvents.CtaView.ScoreTweenStart, playCount, this)
    .on(ViewEvents.CtaView.ScoreTweenComplete, stopCount, this)
    .on(ViewEvents.WinView.ScoreTweenStart, playCount, this)
    .on(ViewEvents.WinView.ScoreTweenComplete, stopCount, this)
    .on(ViewEvents.BetView.ButtonClick, playClick, this)
    .on(ViewEvents.SpinView.Spin, playClick, this)
    .on(ViewEvents.SpinView.Stop, playClick, this)
    .on(ViewEvents.CtaView.PlayClick, playClick, this)
    .on(ViewEvents.PersistentCtaView.Click, playClick, this)
    .on(ViewEvents.SoundView.Click, playClick, this);
}
