import { CoinFallParticle } from '../utils/emitters/coin-fall/coin-fall-particle';

export function getCtaCoinEmitterConfig() {
  return {
    area: {
      random: true,
      shape: new Phaser.Line(-100, -100, CI_API.game.width + 100, -100)
    },
    repeat: true,
    count: 70,
    gravity: { x: 0, y: 5 },
    scale: { min: 1, max: 1.3 },
    rotation: { min: Math.PI, max: 2 * Math.PI },
    delay: { min: 0, max: 3000 },
    duration: { min: 3000, max: 3000 },
    velocity: { x: { min: 0, max: 0 }, y: { min: 300, max: 700 } },
    particleClass: CoinFallParticle
  };
}

export function getWinCoinEmitterConfig() {
  return {
    area: {
      random: true,
      shape: new Phaser.Line(-100, -100, CI_API.game.width + 100, -100)
    },
    count: 50,
    repeat: false,
    gravity: { x: 0, y: 5 },
    scale: { min: 1, max: 1.3 },
    rotation: { min: Math.PI, max: 2 * Math.PI },
    delay: { min: 0, max: 1400 },
    duration: { min: 2000, max: 2000 },
    velocity: { x: { min: 0, max: 0 }, y: { min: 500, max: 1000 } },
    particleClass: CoinFallParticle
  };
}

export const particles = [];
