import { lego } from '@armathai/lego';
import { checkoutCommand } from '../../player/checkout-command';
import { setSpinButtonActiveCommand } from '../spin/set-spin-button-active-command';

export function slotMachineStateSpinCommand() {
  lego.command
    .execute(checkoutCommand)

    .payload(false)
    .execute(setSpinButtonActiveCommand);
}
