import { getRewardPopupBgImageConfig, getRewardPopupFreeSpinBgImageConfig } from '../../../configs/image-configs';
import { getRewardPopupCountTextConfig, getRewardPopupTitleTextConfig } from '../../../configs/text-configs';
import { makeImage, makeText } from '../../../utils';
import { Container } from '../../../utils/container';

export class RewardPopup extends Container {
  constructor() {
    super();

    this._bg = null;

    this._build();
  }

  getBounds() {
    return this._bg.getBounds();
  }

  _build() {
    // BG
    const bg = new Container();
    const part1 = makeImage(getRewardPopupBgImageConfig());
    const part2 = makeImage(getRewardPopupBgImageConfig());

    part2.x += part2.width / 2 - 1;
    part1.x -= part1.width / 2 - 1;

    part2.scale.x = -1;

    bg.addMultiple([part1, part2]);
    this.addChild((this._bg = bg));

    // COUNT
    const countBg = makeImage(getRewardPopupFreeSpinBgImageConfig());
    const count = makeText(getRewardPopupCountTextConfig(CI_API.Globals.PARAMS.number_of_extra_spins));

    count.anchor.set(0.5);

    countBg.addChild(count);
    this.addChild(countBg);

    // TITLE
    const title = makeText(getRewardPopupTitleTextConfig());
    title.anchor.set(0.5);
    this.addChild(title);
  }
}
